import React, { useState, useRef, useEffect } from "react";
import Form from "./Form";
import Options from "./Options";
function Home() {
  const [state, setState] = useState({
    visible: true
  })
  const [products, setProducts] = useState([]);

  function addProduct(name, description){
  const newProduct = { name: name, description: description}
  setProducts([...products, newProduct]);
  }


  const listProducts = products.map(i => (
          <li> {i} </li>
        ));
 

  
  
  return (
    <div className>
      <h1 className="center" >Project</h1>
      
      <button type="Button" className="btn" onClick={() => {setState({visible: true}); }}>
        Add Items
      </button>
      <button type="button" onClick={() => {setState({visible: false}); }}>
        Show all Items
      </button>
      {state.visible ? <Form addProduct={addProduct} /> : {listProducts}}
    
    </div>
  );
}

export default Home;
