import React, { useState } from "react";

function Form(props) {
  const [product, setProduct] = useState({
    name: '',
    description: ''
})
  
  function changeName(e)
  {
    setProduct({name: e.target.value});
  }
  function changeDescription(e){
    setProduct({description: e.target.value});
  }

  function handleSubmit(e) {
    e.preventDefault();
    props.addProduct(product.name, product.description);
    setProduct({name: "",
              description: ""});
  }
  return (
    <form onSubmit={handleSubmit}>
      <h2>
        <label>
          Product Name:
        </label>
      </h2>
      <input
        type="text"
        name="text"
        className = "center"
        value={product}
        onChange={changeName}
      />
      <h2>
        <label>
          Description:
        </label>
      </h2>
      <input
        type="text"
        name="text"
        className="center"
        value={product}
        onChange={changeDescription}
      />
      <button type="submit">
        Submit
      </button>
    </form>
  );
}

export default Form;